.grid {
    border: 2px white;
    height: 100%;;
    margin: auto;
    width: 1200px;
    display: grid;
    grid-template-columns: 25% 25% 2fr 2fr 4fr;
    grid-template-rows: repeat(4, 100px);
    grid-gap: 10px 15px;
  }
  
  .a {
    grid-area: 1 / 1 / span 2 / span 2;
    background-image: url(/PublishedHomepage/static/media/historicalTimeline.473b3032.png);
    background-size: cover;
  }
  .b {
    grid-area: 1 / 3 / span 2 / span 2;
    background-color: rgb(100, 100, 93);
    
  }
  .c {
    grid-area: 1 / 5 / span 2 / span 1;
    background-color: rgb(83, 83, 13);
  }
  .d {
    grid-area: 3 / 1 / span 2 / span 1;
    background-color: rgb(247, 247, 99);
    background-image: url(/PublishedHomepage/static/media/OCEAN.53488488.png);
    background-size: cover;
  }
  .e {
    grid-area: 3 / 2 / span 1 / span 2;
    background-color: rgb(236, 236, 236);
  }
  .f {
    grid-area: 4 / 2 / span 1 / span 2;
    background-color: rgb(212, 212, 18);
  }

  .g {
    grid-area: 3 / 4 / span 2 / span 2;
    background-color: rgb(212, 212, 18);
  }

/*   .h {
