  .skillbar {
      position:relative;
      
      width: 90%;
      left: 50px;
      right: 50px;
      background:#eee;
      height:50px;
      border-radius:3px;
      -moz-border-radius:3px;
      -webkit-border-radius:3px;
      -webkit-transition:0.4s linear;
      transition:0.4s linear;
      -webkit-transition-property:width, background-color;
      transition-property:width, background-color;
  }
  
  .skillbar:not(:last-of-type) {
    margin-bottom:30px;
  }

  .skillbar-title {
      position:absolute;
      top:0;
      left:0;
      width:160px;
      font-weight:bold;
      font-size:20px;
      color:#ffffff;
      background:#6adcfa;
      -webkit-border-top-left-radius:3px;
      -webkit-border-bottom-left-radius:4px;
      -moz-border-radius-topleft:3px;
      -moz-border-radius-bottomleft:3px;
      border-top-left-radius:3px;
      border-bottom-left-radius:3px;
    
      
  }
  
  .skillbar-title span {
      
      display:block;
      background:rgba(0, 0, 0, 0.1);
      height:50px;
      left: 10px;
      line-height: 50px;
      padding-left: 15px;
      -webkit-border-top-left-radius:3px;
      -webkit-border-bottom-left-radius:3px;
      -moz-border-radius-topleft:3px;
      -moz-border-radius-bottomleft:3px;
      border-top-left-radius:3px;
      border-bottom-left-radius:3px;
  }

  .skillbar-title span i {
    position: relative;
    top: 10%;
    font-size: 30px;
    left: -5px;
  }
  
  .skillbar-bar {
      height:50px;
      width:0px;
      background:#6adcfa;
      border-radius:3px;
      -moz-border-radius:3px;
      -webkit-border-radius:3px;
  }
  
  .skill-bar-percent {
      position:absolute;
      right:10px;
      top:0;
      font-size:25px;
      height:35px;
      line-height:50px;
      color:#ffffff;
      color:rgba(0, 0, 0, 0.4);
  }
